<template>
    <div class="guoshun-info" v-title data-title="Lyu | ">
            <el-row :gutter="15">
                <el-form ref="defForm" :model="defForm" :rules="rules"  label-width="100px">
                    <el-col :span="24">
                        <!--  -->
                        <el-form-item label="">
                            <!--  -->
                            <pan-thumb :image="image"/>
                            <!--  -->
                            <el-button type="primary" icon="el-icon-upload" @click="imagecropperShow=true">
                            </el-button>
                            <!--
                              v-show
                              :keyid
                              :urlurl
                              @close
                              @crop-upload-success -->
                            <image-cropper
                                    v-show="imagecropperShow"
                                    :width="300"
                                    :height="300"
                                    :key="imagecropperKey"
                                    :url="'user/upload'"
                                    field="file"
                                    @close="closeImage"
                                    @crop-upload-success="cropSuccess"
                            />
                            <!--@crop-upload-success="cropSuccess"-->

                        </el-form-item>
                    </el-col>
                    <el-col :span="12">
                        <el-form-item label=""  prop="userName">
                            <el-input v-model="defForm.userName"  placeholder="" :disabled="true" clearable :style="{width: '100%'}">
                            </el-input>
                        </el-form-item>
                        <el-form-item label="" prop="nickName">
                            <el-input v-model="defForm.nickName" placeholder="" clearable :style="{width: '100%'}">
                            </el-input>
                        </el-form-item>

                        <el-form-item label="" prop="email">
                            <el-input v-model="defForm.email" placeholder="" clearable :style="{width: '100%'}">
                            </el-input>

                        </el-form-item>

                    </el-col>
                    <el-col :span="15">
                        <el-form-item >
                            <el-button type="primary" :loading="loading" @click="submitForm"></el-button>
                            <el-button @click="close"></el-button>
                        </el-form-item>
                    </el-col>
                </el-form>
            </el-row>
    </div>
</template>

<script>
    import ImageCropper from '../../../components/ImageCropper'
    import token from '@/store/token'
    import {update} from "../../../api/user";
    import PanThumb from '@/components/PanThumb/index'
    export default {
        name: "Info",
        components: {PanThumb,ImageCropper},
        data() {
            return {
                defForm: {
                    id: '',
                    userName:'',
                    nickName: '',
                    avatar: '',
                    email: ''
                },

                loading: false,
                // 
                showDialog:false,
                imagecropperShow: false, // 
                imagecropperKey: 0, // id
                image: 'http://shunxinblog.oss-cn-hangzhou.aliyuncs.com/2020/12/30/c1d5fb12c96c48f0b10d1b5764b73ecd.jpg',
                //https://wpimg.wallstcn.com/577965b9-bb9e-4e02-9f0c-095b41417191
                rules: {
                    nickname: [{required: true, message: '', trigger: 'blur'}],
                    email: [{required: true, message: '', trigger: 'blur'}],
                },
                field101Action: 'https://jsonplaceholder.typicode.com/posts/',
            }
        },
        created(){
            this.defForm=token.getUser()
            this.image=this.defForm.avatar
            if (this.defForm.avatar==null){
                this.image='https://wpimg.wallstcn.com/577965b9-bb9e-4e02-9f0c-095b41417191'
            }
        },
        methods:{
            onClose() {
                this.$refs['elForm'].resetFields()
                this.$emit('changeShow', 'false')
            },
            close() {
                this.$emit('changeShow', 'false')
            },
            submitForm(){
                this.$refs['defForm'].validate(valid=>{
                    if (valid) {
                        this.loading=true
                        update(this.defForm).then(data=>{
                            this.loading=false
                            this.$message.success(data.message)
                            //localStorage
                            token.setUser(data.data)
                            this.defForm=data.data
                        }).catch(error=>{
                            this.loading=true
                            this.$message.error(error)
                        })
                    }
                })
            },
            // 
            cropSuccess(data) {
                //
                this.imagecropperShow = false
                this.defForm.avatar=this.image = data;
                // 
                this.imagecropperKey = this.imagecropperKey + 1
            },
            // 
            closeImage() {
                this.imagecropperShow = false
                // 
                this.imagecropperKey = this.imagecropperKey + 1
            }
        }
    }
</script>

<style scoped>

    .guoshun-info{
        margin-left: 250px;
        margin-top: 30px;
        height: 550px;
    }

</style>